.App {
  text-align: center;
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1{
  margin: 0;
}

.header{
  color: white;
  background-color: #043b5c;
  padding: 14px;
  width: 100%;
}
.form{
  margin-top: 10px;
}

.data{
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  width: 100%;
  align-items: center;
}

.data_val{
  display: flex;
  justify-content: space-between;
  width: 50%;
  padding: 0px 9px 0px 9px;
  align-items: center;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
